import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import AvatarGroup from '@mui/material/AvatarGroup';

const dispatchedEvents = [];

const SrsMuiAvatarGroup = function (props, ref) {
  let componentProps = {...props, theme: props.muiTheme};
  return (
    <AvatarGroup {...componentProps}>
      <slot />
    </AvatarGroup>
  );
}

const styles = `
  /* Custom styles for the AvatarGroup component */
`;

const props = [
  {
    name: "max",
    defaultValue: 5,
    type: "number",
  },
  {
    name: "spacing",
    defaultValue: "small",
    type: "string",
  },
  {
    name: "variant",
    defaultValue: "circular",
    type: "string",
  },
  {
    name: "className",
    defaultValue: "",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object",
  },
  // Add other props specific to AvatarGroup if needed
];

const slots = [
  {
    name: "default",
    allowed: ["srs-mui-avatar"],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiAvatarGroup), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
